import axios from 'axios';

/**
 * /api/init               初始化小车和乘客信息
 * /api/map/getMap
 * /api/car/setCarInfo
 * /api/car/pickPassenger
 * /api/car/dropPassenger
 * /api/showAllInfo
 */

// const API_BASE_URL = 'kevintung.cn:8888';
const API_BASE_URL = "http://localhost:8888";

// 获取初始化的车辆和乘客位置
export const getInitialPositions = async () => {
  try {
    const response = await axios.get(`${API_BASE_URL}/api/init`);
    return response.data;
  } catch (error) {
    console.error('Failed to fetch initial positions:', error);
    throw error;
  }
};

// 获取地图数据
export const getMap = async () => {
  try {
    const response = await axios.get(`${API_BASE_URL}/api/map/select`);
    return response.data;
  } catch (error) {
    console.error('Failed to fetch map data:', error);
    throw error;
  }
};

// 获取车辆信息
export const showAllInfo = async () => {
  try {
    const response = await axios.get(`${API_BASE_URL}/api/car/select/all`);
    return response.data;
  } catch (error) {
    console.error('Failed to fetch car and passenger info:', error);
    throw error;
  }
};

// 接乘客
export const pickPassenger = async (carIndex, passengerIndex) => {
  try {
    const response = await axios.get(`${API_BASE_URL}/api/car/passenger/pick`, {
      params: { carIndex, passengerIndex },
    });
    return response.data;
  } catch (error) {
    console.error('Failed to pick passenger:', error);
    throw error;
  }
};

// 送乘客
export const dropPassenger = async (carIndex, passengerIndex) => {
  try {
    const response = await axios.get(`${API_BASE_URL}/api/car/passenger/drop`, {
      params: { carIndex, passengerIndex },
    });
    return response.data;
  } catch (error) {
    console.error('Failed to drop passenger:', error);
    throw error;
  }
};