<template>
  <div class="layout-container">
  <h1>Map Viewer</h1>

  <div class="content">
    <!-- 地图和动态元素 -->
    <div v-if="grid.length" class="map-container">
      <div class="map">
        <div v-for="(row, rowIndex) in grid" :key="rowIndex" class="row">
          <div
            v-for="(cell, cellIndex) in row"
            :key="cellIndex"
            :class="['cell', cell ? 'black' : 'whiteblack']"
          ></div>
        </div>

        <!-- 渲染车辆 -->
        <img
          v-for="(car, index) in cars"
          :key="index"
          :src="car.image"
          :alt="`Car ${index + 1}`"
          class="car"
          :style="{
            top: `${car.position.y * cellSize}px`,
            left: `${car.position.x * cellSize}px`,
            transform: `rotate(${car.angle}deg)`
          }"
        />

        <!-- 渲染乘客 -->
        <div
          v-for="(passenger, index) in passengers"
          :key="index"
          class="passenger"
          :style="{
            top: `${passenger.position.y * cellSize}px`,
            left: `${passenger.position.x * cellSize}px`,
            backgroundColor: passenger.status === 'WAITING' ? 'red' : 'blue',
          }"
        >
          <span>{{ index }}</span>
        </div>

        <!-- 渲染目的地 -->
        <div
          v-for="(passenger, index) in passengers"
          :key="'dest-' + index"
          class="destination"
          :style="{
            top: `${passenger.destination.y * cellSize}px`,
            left: `${passenger.destination.x * cellSize}px`,
          }"
        >
          D{{ index }}
        </div>
      </div>
    </div>

    <p v-else>Loading map data...</p>

    <!-- 乘客信息表 -->
    <div class="passenger-info">
      <h2>Passenger Information</h2>
      <table class="passenger-table">
        <thead>
          <tr>
            <th>Index</th>
            <th>Start Position</th>
            <th>Destination</th>
            <th>Status</th>
            <th>Target Car</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(passenger, index) in passengers" :key="index">
            <td>{{ passenger.index }}</td>
            <td>{{ passenger.position.x }}, {{ passenger.position.y }}</td>
            <td>{{ passenger.destination.x }}, {{ passenger.destination.y }}</td>
            <td>
              {{ passenger.status === 'WAITING'
                ? "Waiting"
                : `Picked up by Car ${passenger.nowCarIndex}` }}
            </td>
            <td>{{ passenger.nowCarIndex !== undefined ? passenger.nowCarIndex : 'N/A' }}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</div>

</template>

<script>
import { showAllInfo, getMap, getInitialPositions } from "@/api";

export default {
  name: "MapWithCars",
  data() {
    return {
      grid: [], // 存储地图数据
      cars: [
        { image: require("@/assets/red.png"), position: { x: 0, y: 0 }, angle: 0 },
        { image: require("@/assets/yellow.png"), position: { x: 0, y: 0 }, angle: 0 },
        { image: require("@/assets/green.png"), position: { x: 0, y: 0 }, angle: 0 },
        { image: require("@/assets/gray.png"), position: { x: 0, y: 0 }, angle: 0 },
        { image: require("@/assets/blue.png"), position: { x: 0, y: 0 }, angle: 0 },
      ],
      passengers: [], // 乘客数据
      cellSize: 1, // 每个网格单元大小
    };
  },
  methods: {
    async loadMapAndPositions() {
      try {
        // Step 1: 加载地图数据
        const mapResponse = await getMap();
        this.grid = mapResponse;

        // Step 2: 加载车辆和乘客数据
        const positionResponse = await getInitialPositions();
        const { cars, passengers } = positionResponse;

        // 设置车辆数据
        this.cars = cars.map((car, index) => ({
          ...this.cars[index],
          position: { x: car.carX, y: car.carY },
          angle: car.carAngle,
        }));

        // 设置乘客数据
        this.passengers = passengers.map((passenger) => ({
          index: passenger.passengerIndex,
          position: { x: passenger.startX, y: passenger.startY },
          destination: { x: passenger.endX, y: passenger.endY },
          status: passenger.status,
          nowCarIndex: passenger.nowCarIndex,
        }));

        console.log("Map and positions loaded successfully.");
      } catch (error) {
        console.error("Failed to load map and positions:", error);
        alert("Failed to load map or position data!");
      }
    },
    
    async updateCarPositions() {
      try {
        const response = await showAllInfo();
        const cars = response;

        this.cars = this.cars.map((car, index) => ({
          ...car,
          position: { x: cars[index].carX, y: cars[index].carY },
          angle: cars[index].carAngle,
        }));

        console.log("Update car position successfully.");
      } catch (error) {
        console.error("Failed to update car positions:", error);
      }
    },


  },
  mounted() {
    // 自动加载地图和初始数据
    this.loadMapAndPositions();

    // 定时更新车辆位置
    setInterval(this.updateCarPositions, 1000);
  },
};
</script>

<style>
.layout-container {
  display: flex;
  flex-direction: column; 
  align-items: center;
  margin: 10px;
}

.content {
  display: flex;
  flex-direction: row; /* 左右排列 */
  align-items: center;
  gap: 0px; /* 添加间距 */
}

.map-container {
  position: relative;
  width: 800px;
  height: 800px;
}

.map {
  display: grid; /* 使用 grid 布局保证一致性 */
  grid-template-rows: repeat(auto-fit, 1px); /* 单元格高度 */
  grid-template-columns: repeat(auto-fit, 1px); /* 单元格宽度 */
  z-index: 1;
}

.cell {
  width: 1px; /* 单元格大小应与 cellSize 一致 */
  height: 1px;
}

.car {
  position: absolute;
  width: 15px; /* 与单元格大小保持一致 */
  height: 20px;
  z-index: 10;
  transition: all 0.25s ease-in-out;
}

.passenger {
  position: absolute;
  width: 20px; /* 与单元格大小匹配 */
  height: 20px;
  border-radius: 50%;
  z-index: 20;
  transition: all 0.25s ease-in-out;
}

.destination {
  position: absolute;
  width: 20px; /* 与单元格大小匹配 */
  height: 20px;
  border: 2px dashed black;
  border-radius: 50%;
}

.black {
  background-color: black;
}

.white {
  background-color: white;
}

.passenger-info {
  flex: 1;
  overflow-y: auto;
}

.passenger-table {
  border-collapse: collapse;
  width: 100%;
}

.passenger-table th,
.passenger-table td {
  border: 1px solid black;
  padding: 8px;
  text-align: left;
}
</style>